'use client';

import { useState } from 'react';
import { useFindMany } from '@/hooks/useDynamicGraphQL';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Badge } from '@/components/ui/badge';
import { 
  Search, 
  Users,
  BookOpen,
  AlertCircle,
  TrendingUp,
  Calendar,
  DollarSign,
  CheckCircle,
  XCircle,
  Clock
} from 'lucide-react';
import { useToast } from '@/hooks/use-toast';

interface Enrollment {
  id: string;
  status: string;
  progress: number;
  paymentAmount: number;
  paymentMethod: string;
  enrolledAt: string;
  completedAt: string;
  lastAccessedAt: string;
  user: {
    id: string;
    username: string;
    firstName: string;
    lastName: string;
    email: string;
  };
  course: {
    id: string;
    title: string;
    slug: string;
    price: number;
  };
}

export default function AdminEnrollmentsPage() {
  const { toast } = useToast();
  const [searchQuery, setSearchQuery] = useState('');
  const [filterStatus, setFilterStatus] = useState<'all' | 'ACTIVE' | 'COMPLETED' | 'DROPPED'>('all');

  const { data: enrollments, loading, error, refetch } = useFindMany('Enrollment', {
    select: {
      id: true,
      status: true,
      progress: true,
      paymentAmount: true,
      paymentMethod: true,
      enrolledAt: true,
      completedAt: true,
      lastAccessedAt: true,
    },
    include: {
      user: {
        select: {
          id: true,
          username: true,
          firstName: true,
          lastName: true,
          email: true,
        },
      },
      course: {
        select: {
          id: true,
          title: true,
          slug: true,
          price: true,
        },
      },
    },
    orderBy: {
      enrolledAt: 'desc',
    },
  });

  // Filter enrollments
  const filteredEnrollments = (enrollments || []).filter((enrollment: Enrollment) => {
    const userName = enrollment.user.firstName && enrollment.user.lastName
      ? `${enrollment.user.firstName} ${enrollment.user.lastName}`
      : enrollment.user.username;
    
    const matchesSearch = 
      userName.toLowerCase().includes(searchQuery.toLowerCase()) ||
      (enrollment.user.email || '').toLowerCase().includes(searchQuery.toLowerCase()) ||
      enrollment.course.title.toLowerCase().includes(searchQuery.toLowerCase());
    
    const matchesFilter = 
      filterStatus === 'all' || 
      enrollment.status === filterStatus;
    
    return matchesSearch && matchesFilter;
  });

  const formatDate = (dateString: string) => {
    if (!dateString) return '-';
    return new Date(dateString).toLocaleDateString('vi-VN', {
      day: '2-digit',
      month: '2-digit',
      year: 'numeric',
    });
  };

  const formatPrice = (price: number) => {
    if (!price) return 'Mi·ªÖn ph√≠';
    return new Intl.NumberFormat('vi-VN', {
      style: 'currency',
      currency: 'VND',
    }).format(price);
  };

  const getStatusBadge = (status: string) => {
    const variants: Record<string, { variant: any; label: string; icon: any }> = {
      ACTIVE: { variant: 'default', label: 'ƒêang h·ªçc', icon: TrendingUp },
      COMPLETED: { variant: 'default', label: 'Ho√†n th√†nh', icon: CheckCircle },
      DROPPED: { variant: 'secondary', label: 'ƒê√£ b·ªè', icon: XCircle },
    };
    
    const config = variants[status] || { variant: 'outline', label: status, icon: Clock };
    const Icon = config.icon;
    
    return (
      <Badge variant={config.variant} className="gap-1">
        <Icon className="w-3 h-3" />
        {config.label}
      </Badge>
    );
  };

  const getProgressColor = (progress: number) => {
    if (progress >= 80) return 'bg-green-500';
    if (progress >= 50) return 'bg-blue-500';
    if (progress >= 20) return 'bg-yellow-500';
    return 'bg-gray-300';
  };

  const getUserName = (user: Enrollment['user']) => {
    if (user.firstName && user.lastName) {
      return `${user.firstName} ${user.lastName}`;
    }
    return user.username;
  };

  // Stats calculation
  const stats = {
    total: enrollments?.length || 0,
    active: enrollments?.filter((e: Enrollment) => e.status === 'ACTIVE').length || 0,
    completed: enrollments?.filter((e: Enrollment) => e.status === 'COMPLETED').length || 0,
    dropped: enrollments?.filter((e: Enrollment) => e.status === 'DROPPED').length || 0,
  };

  return (
    <div className="p-4 sm:p-6 lg:p-8 space-y-6">
      {/* Header */}
      <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4">
        <div>
          <h1 className="text-2xl sm:text-3xl font-bold text-gray-900">Qu·∫£n l√Ω ghi danh</h1>
          <p className="text-sm sm:text-base text-gray-600 mt-1">T·ªïng c·ªông {stats.total} ghi danh</p>
        </div>
      </div>

      {/* Stats Cards */}
      <div className="grid grid-cols-2 lg:grid-cols-4 gap-4">
        <Card>
          <CardHeader className="pb-3">
            <CardDescription>T·ªïng s·ªë</CardDescription>
            <CardTitle className="text-2xl">{stats.total}</CardTitle>
          </CardHeader>
        </Card>
        <Card>
          <CardHeader className="pb-3">
            <CardDescription className="flex items-center gap-1">
              <TrendingUp className="w-4 h-4" />
              ƒêang h·ªçc
            </CardDescription>
            <CardTitle className="text-2xl text-blue-600">{stats.active}</CardTitle>
          </CardHeader>
        </Card>
        <Card>
          <CardHeader className="pb-3">
            <CardDescription className="flex items-center gap-1">
              <CheckCircle className="w-4 h-4" />
              Ho√†n th√†nh
            </CardDescription>
            <CardTitle className="text-2xl text-green-600">{stats.completed}</CardTitle>
          </CardHeader>
        </Card>
        <Card>
          <CardHeader className="pb-3">
            <CardDescription className="flex items-center gap-1">
              <XCircle className="w-4 h-4" />
              ƒê√£ b·ªè
            </CardDescription>
            <CardTitle className="text-2xl text-gray-600">{stats.dropped}</CardTitle>
          </CardHeader>
        </Card>
      </div>

      {/* Filters */}
      <div className="flex flex-col sm:flex-row gap-4">
        <div className="flex-1 relative">
          <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 w-4 h-4 text-gray-400" />
          <Input
            placeholder="T√¨m ki·∫øm theo h·ªçc vi√™n, kh√≥a h·ªçc..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            className="pl-10"
          />
        </div>
        <div className="flex gap-2 overflow-x-auto pb-2 sm:pb-0">
          <Button
            variant={filterStatus === 'all' ? 'default' : 'outline'}
            onClick={() => setFilterStatus('all')}
            size="sm"
          >
            T·∫•t c·∫£
          </Button>
          <Button
            variant={filterStatus === 'ACTIVE' ? 'default' : 'outline'}
            onClick={() => setFilterStatus('ACTIVE')}
            size="sm"
          >
            ƒêang h·ªçc
          </Button>
          <Button
            variant={filterStatus === 'COMPLETED' ? 'default' : 'outline'}
            onClick={() => setFilterStatus('COMPLETED')}
            size="sm"
          >
            Ho√†n th√†nh
          </Button>
          <Button
            variant={filterStatus === 'DROPPED' ? 'default' : 'outline'}
            onClick={() => setFilterStatus('DROPPED')}
            size="sm"
          >
            ƒê√£ b·ªè
          </Button>
        </div>
      </div>

      {/* Enrollments List */}
      {loading ? (
        <div className="text-center py-12">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto"></div>
          <p className="text-gray-500 mt-4">ƒêang t·∫£i...</p>
        </div>
      ) : error ? (
        <Card>
          <CardContent className="py-12 text-center">
            <AlertCircle className="w-12 h-12 text-red-500 mx-auto mb-4" />
            <p className="text-red-600">L·ªói: {error.message}</p>
          </CardContent>
        </Card>
      ) : filteredEnrollments.length === 0 ? (
        <Card>
          <CardContent className="py-12 text-center">
            <BookOpen className="w-12 h-12 text-gray-400 mx-auto mb-4" />
            <p className="text-gray-500">Kh√¥ng t√¨m th·∫•y ghi danh n√†o</p>
          </CardContent>
        </Card>
      ) : (
        <div className="grid grid-cols-1 gap-4">
          {filteredEnrollments.map((enrollment: Enrollment) => (
            <Card key={enrollment.id} className="hover:shadow-lg transition-shadow">
              <CardHeader className="pb-3">
                <div className="flex flex-col sm:flex-row sm:items-start sm:justify-between gap-3">
                  <div className="flex-1 min-w-0">
                    <div className="flex items-start gap-3">
                      <Users className="w-5 h-5 text-blue-600 mt-1 flex-shrink-0" />
                      <div className="flex-1 min-w-0">
                        <CardTitle className="text-base mb-1">
                          {getUserName(enrollment.user)}
                        </CardTitle>
                        <p className="text-sm text-gray-600 truncate">
                          {enrollment.user.email || enrollment.user.username}
                        </p>
                      </div>
                    </div>
                  </div>
                  {getStatusBadge(enrollment.status)}
                </div>
              </CardHeader>

              <CardContent className="space-y-4">
                {/* Course Info */}
                <div className="flex items-start gap-3 p-3 bg-gray-50 rounded-lg">
                  <BookOpen className="w-5 h-5 text-green-600 mt-0.5 flex-shrink-0" />
                  <div className="flex-1 min-w-0">
                    <p className="font-medium text-gray-900 truncate">{enrollment.course.title}</p>
                    <p className="text-sm text-gray-600 mt-1">
                      Gi√°: {formatPrice(enrollment.course.price)}
                    </p>
                  </div>
                </div>

                {/* Progress Bar */}
                <div className="space-y-2">
                  <div className="flex justify-between text-sm">
                    <span className="text-gray-600">Ti·∫øn ƒë·ªô h·ªçc t·∫≠p</span>
                    <span className="font-semibold text-gray-900">{Math.round(enrollment.progress)}%</span>
                  </div>
                  <div className="w-full bg-gray-200 rounded-full h-2">
                    <div
                      className={`h-2 rounded-full transition-all ${getProgressColor(enrollment.progress)}`}
                      style={{ width: `${enrollment.progress}%` }}
                    />
                  </div>
                </div>

                {/* Payment Info */}
                {enrollment.paymentAmount > 0 && (
                  <div className="flex items-center gap-2 text-sm text-gray-600 p-2 bg-blue-50 rounded">
                    <DollarSign className="w-4 h-4 text-blue-600" />
                    <span>Thanh to√°n: {formatPrice(enrollment.paymentAmount)}</span>
                    {enrollment.paymentMethod && (
                      <Badge variant="outline" className="ml-auto">
                        {enrollment.paymentMethod}
                      </Badge>
                    )}
                  </div>
                )}

                {/* Timeline */}
                <div className="grid grid-cols-1 sm:grid-cols-3 gap-3 pt-3 border-t text-xs">
                  <div className="flex items-center gap-2 text-gray-600">
                    <Calendar className="w-3 h-3" />
                    <div>
                      <p className="text-gray-500">Ghi danh</p>
                      <p className="font-medium text-gray-900">{formatDate(enrollment.enrolledAt)}</p>
                    </div>
                  </div>
                  
                  {enrollment.lastAccessedAt && (
                    <div className="flex items-center gap-2 text-gray-600">
                      <Clock className="w-3 h-3" />
                      <div>
                        <p className="text-gray-500">Truy c·∫≠p cu·ªëi</p>
                        <p className="font-medium text-gray-900">{formatDate(enrollment.lastAccessedAt)}</p>
                      </div>
                    </div>
                  )}
                  
                  {enrollment.completedAt && (
                    <div className="flex items-center gap-2 text-gray-600">
                      <CheckCircle className="w-3 h-3 text-green-600" />
                      <div>
                        <p className="text-gray-500">Ho√†n th√†nh</p>
                        <p className="font-medium text-green-900">{formatDate(enrollment.completedAt)}</p>
                      </div>
                    </div>
                  )}
                </div>

                {/* Actions */}
                <div className="flex gap-2 pt-3 border-t">
                  <Button variant="outline" size="sm" className="flex-1">
                    Xem chi ti·∫øt
                  </Button>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      )}
    </div>
  );
}
